	module.exports = {"data":{"nodePost":{"title":"How Did This Get Made - Rad Live","link":"https://www.earwolf.com/episode/rad-live/","body":{"value":"<p>Back in June I got a chance to see a taping of <a href=\"https://www.hdtgminfo.com/\">How Did This Get Made</a>, which is one of my all time favorite podcasts. I was coming home from Texas that day and just barely made it to the show in time because my flight was delayed. Thankfully all of the travel stress was worth it - seeing the show live was amazing, and our 80's BMX Bike movie <a href=\"https://www.imdb.com/title/tt0091817/\">Rad</a> was the best kind of bad movie.</p>\r\n<p>They released <a href=\"https://www.earwolf.com/episode/rad-live/\">the live show as a podcast episode</a> a few weeks back, and it is well worth the listen.</p>"},"relationships":{"image":{"localFile":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.122334455667789,"src":"/static/14d18e39c1645674ba8b255b4e3ef15e-8d559be0a9177e1c64858b4e1639f013-42807.jpg","srcSet":"/static/14d18e39c1645674ba8b255b4e3ef15e-8d559be0a9177e1c64858b4e1639f013-960f9.jpg 313w,\n/static/14d18e39c1645674ba8b255b4e3ef15e-8d559be0a9177e1c64858b4e1639f013-c36f2.jpg 625w,\n/static/14d18e39c1645674ba8b255b4e3ef15e-8d559be0a9177e1c64858b4e1639f013-42807.jpg 1250w,\n/static/14d18e39c1645674ba8b255b4e3ef15e-8d559be0a9177e1c64858b4e1639f013-dbc5e.jpg 1875w,\n/static/14d18e39c1645674ba8b255b4e3ef15e-8d559be0a9177e1c64858b4e1639f013-97be6.jpg 2000w","sizes":"(max-width: 1250px) 100vw, 1250px"}}}}},"created":1531595780}},"pathContext":{"slug":"/link/how-did-this-get-made-rad-live/"}}

